        <div class="header text-right">
            <img src="@/assets/mipango-logo.png" alt="Mipango Initiative Logo" class="logo" />
            <div class="title-container position-relativet">
                <h2 class="title">SEMINA YA KIUCHUMI</h2>
                <h4 class="subtitle">15-21 Februari</h4>
            </div>
        </div>

        <a-card class="text-left" bordered="false">
            <h5 class="pb-8">
                Mipango Initiative ni juhudi inayolenga kuwapa vijana na wanawake stadi za kiuchumi na ujasiriamali ili
                kuwawezesha kujikwamua kiuchumi na kuchangia maendeleo ya uchumi wa nchi. Mafunzo yanayopatikana ni:
