                    </ul>
                </li>
                <li><strong>Stadi za Kiufundi na Ufundi:</strong>
                    <ul>
                        <li>Ushonaji na utengenezaji wa nguo</li>
                        <li>Ufundi wa umeme na vifaa vya nyumbani</li>
                        <li>Ujenzi na ukarakashi</li>
                        <li>Utengenezaji wa bidhaa za asili (handicrafts)</li>
                        <li>Ufundi wa mitambo na magari</li>
                    </ul>
                </li>
                <li><strong>Kilimo na Ufugaji:</strong>
                    <ul>
                        <li>Kilimo cha kisasa na mbinu bora za kilimo</li>
                        <li>Ufugaji wa mifugo na usimamizi wa malisho</li>
