            <h5 class="pt-8">
                Kujiunga na SEMINA hii tfadhali jaza taarifa zifuatazo:
            </h5>

            <a-form :model="formState" @finish="onFinish">
                <a-form-item label="Jina lako" name="firstName"
                    :rules="[{ required: true, message: 'Please input your first name!' }]">
                    <a-input v-model:value="formState.firstName" />
                </a-form-item>

                <a-form-item label="Jina la ukoo" name="lastName"
                    :rules="[{ required: true, message: 'Please input your last name!' }]">
                    <a-input v-model:value="formState.lastName" />
                </a-form-item>

                <a-form-item label="Simu" name="phoneNumber"
                    :rules="[{ required: true, message: 'Please input your phone number!' }]">
                    <a-input v-model:value="formState.phoneNumber" />
                </a-form-item>

                <a-form-item class="text-center">
                    <a-button type="primary" html-type="submit">Tuma taarifa</a-button>
                </a-form-item>
            </a-form>
        </a-card>
    </div>
</template>

<script>
import { defineComponent, reactive } from "vue";
import { db } from "@/firebase";
import { collection, addDoc } from "firebase/firestore";

export default defineComponent({
    setup() {
        const formState = reactive({
            firstName: "",
            lastName: "",
            phoneNumber: "",
        });

        const onFinish = async (values) => {
            try {
                const submissionData = {
                    ...values,
                    submissionDate: new Date().toISOString(),
                };
                const docRef = await addDoc(collection(db, "applications"), submissionData);
                console.log("Document written with ID: ", docRef.id);
                alert("Form submitted successfully!");
            } catch (error) {
                console.error("Error adding document: ", error);
                alert("Failed to submit form. Please try again.");
            }
        };

        return {
            formState,
            onFinish,
        };
    },
});
</script>

